
<template>
  <div>

    <!-- IF !CLOSABLE -->
    <div v-if="!closable" class="group_notifs no_closable">
      <div class="title no-select">
        <span class="icon"><i :class="group.icon"></i></span>
        <span class="_label">{{ group.label }}</span>
      </div>
      <div class="container_notifs">
        <div class="notifications">
          <label v-for="(paramNotif, key) in group.list" class="item_notif" :key="'option_' + key">
            <span class="_label">{{ paramNotif.label }}</span>
            <OptionCheckbox :checked="paramNotif.value" @change="change" :key="'paramNotif_' + $vnode.key + '_' + key"></OptionCheckbox>
          </label>
        </div>
      </div>
    </div>

    <!-- CLOSABLE -->
    <div v-else :class="{ 'open': open || !closable }" class="group_notifs">
      <div class="title no-select" @click="toggle">
        <span class="icon"><i :class="group.icon"></i></span>
        <span class="_label">{{ group.label }}</span>
        <span class="container_buttons">
          <div v-if="!open" class="_caret"><i class="fal fa-angle-down"></i></div>
          <span v-else class="index_columns" @click.stop>
            <span v-for="(caption, key) in captions" :key="key" :data-title="caption.tooltip" @click="selectAllColumn([$vnode.key, caption.key])"><i class="fa-fw" :class="caption.icon"></i></span>
          </span>
        </span>
      </div>
      <transition name="anim_show">
        <div v-if="open" class="container_notifs">
          <div class="notifications">

            <span v-for="(paramNotif, key) in group.list" :key="key">
              <div class="item_notif">
                <span class="_label">{{ paramNotif.label }}</span>
                <span class="container_buttons">
                  <Checkboxs :values="paramNotif.value" class="index_columns" @change="change" :key="'paramNotif_' + $vnode.key + '_' + key"></Checkboxs>
                </span>
              </div>

              <div v-for="(option, subkey) in paramNotif.options" class="item_notif item_notif_option" :key="'option_' + subkey">
                <OptionCheckbox :checked="option.value" @change="change" :disabled="!paramNotif.value.default && !paramNotif.value.device && !paramNotif.value.email" :key="'option_' + $vnode.key + '_' + key +'_' + subkey"></OptionCheckbox>
                <span class="_label label_option" :class="{ '_disabled' : !paramNotif.value.default && !paramNotif.value.device && !paramNotif.value.email }">{{ option.label }}</span>
                <span class="container_buttons"><div class="index_columns"><span></span><span></span><span></span><span></span></div></span>
              </div>
            </span>

          </div>
        </div>
      </transition>
    </div>

  </div>
</template>

<script>

import Checkboxs from '../checkboxs/checkboxs'
import EventCheckbox from '@/components/pages/notifications/checkboxs/event_checkbox'
import OptionCheckbox from '@/components/pages/notifications/checkboxs/option_checkbox'

export default {
  name: 'NotificationsGroup',
  components: {
    EventCheckbox,
    OptionCheckbox,
    Checkboxs
  },
  data () {
    return {
      open: false,
      test: { default: true, device: false, email: false }
    }
  },
  props: {
    captions: {
      required: true,
      type: Object / Array,
      default: {}
    },
    group: {
      required: true,
      type: Object / Array,
      default: {}
    },
    closable: {
      required: false,
      type: Boolean,
      default: false
    }
  },
  methods: {
    toggle () {
      if (this.closable === true) this.open = !this.open
    },
    close () {
      if (this.closable === true) this.open = false
    },
    change (key, value) {
      this.$emit('change', key, value)
    },
    selectAllColumn (key) {
      this.$emit('selectAllColumn', key)
    }
  }
}

</script>

<style scoped>

.group_notifs {
  background-color: #ebeef6;
  border-radius: 10px;
  margin-bottom: 25px;
}
.open.group_notifs {
  background-color: #fff;
}
.title {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 42px;
  padding: 0 0 0 20px;
  border-radius: 10px;
  background-color: #ebeef6;
  cursor: pointer;
  align-items: stretch;
}
.open.group_notifs .title {
  background-color: #fff;
}
.title .icon {
  display: flex;
  width: 30px;
  text-align: center;
  align-items: center;
}
.title ._label {
  display: flex;
  flex: 1;
  padding: 0 10px;
  overflow: hidden;
  font-weight: bold;
  font-size: 1.1em;
  text-overflow: ellipsis;
  white-space: nowrap;
  align-items: center;
}
.container_buttons {
  display: flex;
  align-items: center;
  height: 100%;
}
.title .container_buttons ._caret {
  display: flex;
  height: 100%;
  width: 20px;
  margin-right: 15px;
  align-items: center;
  justify-content: center;
  transition: .1s;
  transform: scaleY(1);
}
.container_buttons .index_columns {
  display: flex;
  flex-direction: row;
  /*height: 100%;*/
  height: 42px;
  cursor: default;
}
.container_buttons .index_columns > * {
  display: flex;
  width: 40px;
  height: 100%;
  align-items: center;
  justify-content: center;
  /*border-left: 1px solid #f6f7fb;*/
  cursor: pointer;
}
.container_buttons .index_columns > /deep/ *:first-child {
  border-left: 1px solid #f6f7fb;
}
.container_buttons .index_columns > /deep/ *:nth-child(2) {
  border-left: 1px solid #f6f7fb;
}
.container_notifs {
  overflow: hidden;
}
.container_notifs .notifications .item_notif {
  display: flex;
  flex-direction: row;
  padding: 0 0 0 60px;
  align-items: center;
  height: 42px;
  font-weight: normal;
  margin: 0px;
}
.container_notifs .notifications .item_notif ._label {
  flex: 1;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding: 10px 0;
}
.container_notifs .notifications .item_notif.item_notif_option {
  align-items: stretch;
  height: auto;
}
.container_notifs .notifications .item_notif ._label.label_option {
  white-space: normal;
  padding: 5px;
  transition: .1s;
}
.container_notifs .notifications .item_notif ._label.label_option._disabled {
  opacity: .5;
  cursor: not-allowed;
}
/**/
.no_closable,
.no_closable .title {
  background-color: transparent !important;
  cursor: default !important;
}

/* ANIM */
.anim_show-enter-active, .anim_show-enter-active .notifications { transition: .1s; }
.anim_show-leave-active, .anim_show-leave-active .notifications { transition: .05s; }

.anim_show-enter .notifications,
.anim_show-leave-to .notifications {
  opacity: 0;
  transform: translateY(-10px);
}
.anim_show-leave .notifications,
.anim_show-enter-to .notifications {
  opacity: 1;
  transform: translateY(0px);
}

</style>



