
<template>

  <div class="container_form">
    <div v-for="(line,key) in form" class="line" :key="key">

      <div v-for="(input,key) in line" :key="key">

        <label v-if="input.type !== 'list-checkbox' && input.type !== 'file'">

          <!-- LABEL -->
          <div v-if="input.labelHtml" v-html="input.labelHtml"></div>
          <div v-else>{{ input.label }}</div>

          <!-- SELECT (CHOICES.JS) -->
          <select v-if="input.type == 'select'" v-model="input.value" :name="input.name" :multiple="input.multiple === true ? 'multiple' : false" :id="input.id" :ref="input.ref">
            <slot v-if="input.optgroup">
              <optgroup v-for="(options, key) in input.optgroup" :label="options.label" :key="'optgroup_' + key">
                <option v-for="(option, key) in options.options" :value="option.value" :key="'option_' + key">{{ option.text }}</option>
              </optgroup>
            </slot>
            <slot v-else>
              <option v-for="(option, key) in input.options" :value="option.value" :key="key">{{ option.text }}</option>
            </slot>
          </select>

          <!-- PASSWORD -->
          <div v-else-if="input.type == 'password'" class="input_password">
            <input :type="input.type" :value="input.value" autocomplete="off" v-model="input.value" :name="input.name" :disabled="input.disabled === true ? true : false">
            <div class="icon_view" @click.prevent="toggleInputPassword"><i class="fal fa-eye"></i><i class="fal fa-eye-slash"></i></div>
          </div>

          <!-- DIV -->
          <div v-else-if="input.type == 'div'"></div>

          <!-- SPAN -->
          <div v-else-if="input.type == 'span'"></div>

          <!-- INPUT -->
          <input v-else :type="input.type" :value="input.value" v-model="input.value" :name="input.name" :disabled="input.disabled === true ? true : false">

        </label>

        <div v-else>

          <span v-if="input.type == 'list-checkbox'">
            <div v-if="input.labelHtml" v-html="input.labelHtml"></div>
            <div v-else>{{ input.label }}</div>
            <div class="checkbox_list">
              <label v-for="(checkbox, key) in input.list" :key="key">
                <span v-if="checkbox.icon" class="icon"><i class="fa-fw" :class="checkbox.icon"></i></span>
                <span class="text">{{ checkbox.text }}</span>
                <Checkbox :checked="checkbox.checked" :name="checkbox.name" class="checkbox"></Checkbox>
              </label>
            </div>
          </span>

          <!-- INPUT FILE -->
          <span v-else-if="input.type == 'file'">
            <div v-if="input.labelHtml" v-html="input.labelHtml"></div>
            <div v-else>{{ input.label }}</div>
            <label class="input_file">
              <input id="avatar" type="file" @change="readImage" />
              <input type="hidden" ref="avatar_base64" :name="input.name" />
              <input type="hidden" ref="avatar_base64_url" :name="input.name+'_withurl'" />
              <div class="preview">
                <img :src="input.value" class="preview_avatar">
              </div>
              <div class="filename">
                <span class="name" ref="avatar_name">{{ input.value.split(/(\\|\/)/g).pop() }}</span>
                <span class="remove" @click.prevent.stop="emptyInput"><i class="fal fa-times"></i></span>
              </div>
              <div class="button">
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><path fill="currentColor" d="M296 384h-80c-13.3 0-24-10.7-24-24V192h-87.7c-17.8 0-26.7-21.5-14.1-34.1L242.3 5.7c7.5-7.5 19.8-7.5 27.3 0l152.2 152.2c12.6 12.6 3.7 34.1-14.1 34.1H320v168c0 13.3-10.7 24-24 24zm216-8v112c0 13.3-10.7 24-24 24H24c-13.3 0-24-10.7-24-24V376c0-13.3 10.7-24 24-24h136v8c0 30.9 25.1 56 56 56h80c30.9 0 56-25.1 56-56v-8h136c13.3 0 24 10.7 24 24zm-124 88c0-11-9-20-20-20s-20 9-20 20 9 20 20 20 20-9 20-20zm64 0c0-11-9-20-20-20s-20 9-20 20 9 20 20 20 20-9 20-20z"></path></svg>
              </div>
            </label>
          </span>

        </div>

      </div>

    </div>
  </div>

</template>

<script>

import Checkbox from '@/components/form/checkbox/checkbox_interface'
export default {
  name: 'Form',
  components: {
    Checkbox
  },
  props: {
    form: {
      required: false,
      type: Object / Array,
      default: {}
    }
  },
  mounted () {
    const selects = document.querySelectorAll('select')
    for (var i = 0; i < selects.length; i++) {
      /* eslint-disable-next-line */
      new Choices(selects[i], { searchEnabled: true, shouldSort: false })
    }
    /*  INTERFACE LANGUAGES */
    var language_refs = this.$refs['selectInterfaceLanguage']
    var select = language_refs ? language_refs[0] : undefined
    if (select) {
      select.addEventListener('change', function (event) {
        if (event.currentTarget.value !== 'en' && event.currentTarget.value !== 'fr') {
          window.swal({
            title: 'Help us improve our languages!',
            text: 'This language is a beta version.<br><br>If you have any suggestions that could help us improve it, reach out to us!<br><br>Thanks!',
            showCancelButton: false,
            html: true,
            confirmButtonColor: '#E01765',
            confirmButtonText: window.I18n.t('customization.swal.ok_btn')
          })
        }
      })
    }
  },
  methods: {
    toggleInputPassword (event) {
      event.currentTarget.parentNode.children[0].type = event.currentTarget.parentNode.children[0].type === 'text' ? 'password' : 'text'
      event.currentTarget.parentNode.children[0].focus()
    },
    /* INPUT FILE */
    readImage (event) {
      const file = event.target.files[0]
      if (file.type && file.type.indexOf('image') === -1) {
        console.log('File is not an image.', file.type, file)
        return
      }
      const reader = new FileReader()
      reader.addEventListener('load', function (event) {
        window.avatars = document.querySelectorAll('.preview_avatar')
        for (var i = 0; i < window.avatars.length; i++) {
          window.avatars[i].src = event.target.result
        }
        this.$refs['avatar_base64'][0].value = event.target.result
        this.$refs['avatar_name'][0].innerHTML = file.name
      }.bind(this))
      // reader.addEventListener('progress', (event) => {
      // if (event.loaded && event.total) {
      //   const percent = (event.loaded / event.total) * 100
      //   console.log(`Progress: ${Math.round(percent)}`)
      //   }
      // })
      reader.readAsDataURL(file)
    },
    getUrlAvatarDefault () {
      var initial = [ window.current_user.firstname.length > 0 ? window.current_user.firstname[0] : '', window.current_user.lastname.length > 0 ? window.current_user.lastname[0] : '' ]

      var bgColorAvatar = '%2Fd5f6fd'
      var textColorAvatar = '%2F0abde3'
      return 'https://www.gravatar.com/avatar/d45fb7816136ba3d3c6e68b74c2c904c?s=120&d=https%3A%2F%2Fui-avatars.com%2Fapi%2F' + initial[0] + '%2520' + initial[1] + '%2F128' + bgColorAvatar + textColorAvatar
    },
    emptyInput () {
      var urlAvatar = this.getUrlAvatarDefault()
      window.avatars = document.querySelectorAll('.preview_avatar')
      for (var i = 0; i < window.avatars.length; i++) { window.avatars[i].src = urlAvatar }
      this.$refs['avatar_base64_url'][0].value = urlAvatar
      this.$refs['avatar_base64'][0].value = ''
      this.$refs['avatar_name'][0].innerHTML = ''
    }
  }
}

</script>

<style scoped>

.container_form {
  display: flex;
  flex-direction: column;
}
.container_form .line {
  display: flex;
  flex: 1;
  flex-direction: row;
  margin-bottom: 30px;
}
.container_form .line > * {
  display: flex;
  flex: 1;
  flex-direction: row;
  font-weight: bold;
  margin: 0 10px;
}
.container_form .line > *,
.container_form .line > * > * {
  min-width: 0;
}
.container_form .line > *:first-child {
  margin-left: 0;
}
.container_form .line > *:last-child {
  margin-right: 0;
}
.container_form .line > * > div,
.container_form .line > * label {
  margin: 0;
  flex: 1;
}
.container_form .line > * input {
  width: 100%;
  padding: 11px 12px;
  font-family: inherit;
  font-size: inherit;
  font-weight: normal;
  color: inherit;
  border-radius: 10px;
  border: none;
  outline: none;
  transition: .1s;
  margin-top: 5px;
  background-color: #f6f7fb;
}
.container_form .line > * input:focus {
  background-color: #ebeef6;
}
.container_form .line > * input:disabled {
  opacity: .5;
  cursor: not-allowed;
}

/* INPUT FILE */
.container_form .line > * .input_file {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  border-radius: 10px;
  background-color: #f6f7fb;
  margin-top: 5px;
}
.container_form .line > * .input_file input {
  position: absolute;
  visibility: hidden;
  width: 0px;
  height: 0px;
}
.container_form .line > * .input_file .preview {
  height: 44px;
  width: 44px;
  border-radius: inherit;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  padding: 2px;
}
.container_form .line > * .input_file .preview img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: inherit;
}
.container_form .line > * .input_file .filename {
  flex: 1;
  display: flex;
  flex-direction: row;
  min-width: 0;
}
.container_form .line > * .input_file .filename .name {
  display: flex;
  align-items: center;
  flex: 1;
  padding: 0 10px;
  font-weight: normal;
  min-width: 0;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.container_form .line > * .input_file .filename .remove {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 30px;
  width: 30px;
  opacity: .5;
  font-size: .9em;
  transition: .1s;
  cursor: pointer;
}
.container_form .line > * .input_file .filename .remove:hover {
  opacity: .8;
}
.container_form .line > * .input_file .filename .name:empty ~ .remove {
  display: none;
}
.container_form .line > * .input_file .filename .name ~ .remove {
  display: flex;
}
.container_form .line > * .input_file .button {
  display: flex;
  height: 40px;
  width: 40px;
  align-items: center;
  justify-content: center;
  border-radius: inherit;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  cursor: pointer;
  background-color: #e5f4fd;
  color: #0f9aee;
  margin: 2px;
}
.container_form .line > * .input_file .button svg {
  height: 16px;
  width: 16px;
}

/* INPUT PASSWORD */
.input_password {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.input_password .icon_view {
  display: flex;
  height: 30px;
  width: 30px;
  margin-left: -30px;
  align-items: center;
  cursor: pointer;
}
.input_password .icon_view i {
  display: none;
}
.input_password input[type=text] ~ .icon_view i:last-child,
.input_password input[type=password] ~ .icon_view i:first-child {
  display: block;
}

/**/
.checkbox_list {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
}
.checkbox_list > label {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px 15px;
  font-weight: normal;
  transition: .1s;
  border-radius: 10px;
}
.checkbox_list > label:hover {
  background-color: #e5f4fd;
}
.checkbox_list > label .icon {
  margin-right: 10px;
}
.checkbox_list > label .text {
  flex: 1;
}
.checkbox_list > label .checkbox {
  height: 15px;
  width: 15px;
}

</style>

<style>

/* OVERRIDE CSS CHOICES.JS (for select) */
.choices__inner {
  border: none !important;
  background-color: #f6f7fb !important;
  border-radius: 10px !important;
  margin-top: 5px;
  font-weight: normal !important;
}
.is-open .choices__inner {
  border-radius: 10px 10px 0 0 !important;
}
.choices__list--dropdown {
  border-color: #f6f7fb !important;
  border-bottom: none !important;
  background-color: #f6f7fb !important;
  border-radius: 0 0 10px 10px !important;
  font-weight: normal !important;
}
.is-open .choices__list--dropdown {
  border-color: #f6f7fb !important;
}
.choices__list--dropdown .choices__item--selectable.is-highlighted {
  background-color: #e5f4fd !important;
}
.choices[data-type*="select-one"] .choices__input {
  border-bottom-color: #e5f4fd !important;
}
.choices__input {
  background-color: transparent !important;
  vertical-align: top !important;
  margin: 0px !important;
}

/**/
.icon_help {
  height: 15px;
  width: 15px;
}
.create_avatar {
  color: #0f9aee;
}
.create_avatar:hover {
  text-decoration: underline;
}

</style>



