
<template>
  <form class="_container" action="" method="post" ref="form_infos" @submit.prevent.stop="submit">

    <!-- FORM -->
    <div class="form">
      <Form :form="form"></Form>
    </div>

    <!-- STICKY -->
    <div class="container_sticky">
      <div class="content">
        <button type="submit" class="btn btn-success save">{{ buttons.save }}</button>
        <button type="button" class="btn btn-info btn-inverse schedules">{{ buttons.schedules }}</button>
        <img :src="image" class="illustration">
      </div>
    </div>

  </form>
</template>

<script>

import Form from '@/components/form/form'
export default {
  name: 'Infos',
  components: {
    Form
  },
  data () {
    return {
      buttons: {
        schedules: window.I18n.t('collaborators.edit.edit_timetable'),
        save: window.I18n.t('onboarding.buttons.save')
      },
      formated_timezones: window.timezones,
      form: [
        [
          { type: 'text', label: window.I18n.t('team.edit.firstname'), name: 'firstname', value: window.current_user.firstname },
          { type: 'text', label: window.I18n.t('team.edit.lastname'), name: 'lastname', value: window.current_user.lastname },
          { type: 'text', label: window.I18n.t('team.edit.nickname'), name: 'nickname', value: window.current_user.nickname }
        ],
        [
          { type: 'email', label: window.I18n.t('team.edit.email'), name: 'email', value: window.current_user.email }
        ],
        [
          { type: 'text', label: window.I18n.t('roles.index.role'), name: 'role', value: window.current_user.role, disabled: true }
        ],
        [
          { type: 'select', labelHtml: window.I18n.t('team.edit.select_widgets') + ' <img src="/icons/question-circle-solid.svg" class="icon_help" data-title="' + window.I18n.t('collaborators.edit.can_change_brands') + '" data-direction-default="top"/>', name: 'widgets', multiple: true, value: window.current_user.widgets, options: window.widgets }
        ],
        [
          { type: 'file', labelHtml: window.I18n.t('team.edit.avatar_upload') + '<br>' + window.I18n.t('team.edit.avatar_create') + ' <a href="avatar" class="create_avatar">' + window.I18n.t('team.edit.yours') + '</a>', name: 'avatar', value: window.current_user.avatar }
        ],
        [
          window.appName === 'Slaask'
            ? { type: 'span', labelHtml: window.I18n.t('team.edit.timezone') + ' <span style="font-weight: normal;">' + window.formated_timezone + ' (<a style="font-size: .8em;" href="https://slack.com/account/settings" target="_blank">' + window.I18n.t('team.edit.change') + '</a>)</span>' }
            : { type: 'select', label: window.I18n.t('team.edit.timezone'), name: 'xeno_timezone', value: window.current_user.xeno_timezone, options: window.timezones }
        ],
        [
          { type: 'select', label: window.I18n.t('team.edit.select_lang'), name: 'languages', multiple: true, value: window.current_user.language_ids, options: window.spoken_languages }
        ]
      ],
      image: window.appName === 'Slaask' ? '/edit/edit-name-slaask.svg' : '/edit/edit-name-xeno.svg'
    }
  },
  methods: {
    submit () {
      this.$emit('submit', new FormData(this.$refs['form_infos']), 'user', 'informations', res => {
        if (res.data.success) {
          window.notifyWithTimeout('success', window.I18n.t(`collaborators.edit.update_informations_success`), 1500)
        } else {
          window.notifyWithTimeout('error', window.I18n.t(`collaborators.edit.error_informations_${res.data.error}`), 1500)
        }
      })
    }
  }
}

</script>

<style scoped>

._container {
  display: flex;
  flex-direction: row;
}
._container .form {
  background-color: #fff;
  border-radius: 10px;
  padding: 30px;
  flex: 1;
  min-width: 0;
}
._container .container_sticky {
  margin-left: 25px;
  width: 250px;
}
._container .container_sticky .content {
  position: sticky;
  top: 15px;
}
._container .container_sticky .content .illustration {
  width: 100%;
}
._container .container_sticky .content button {
  display: block;
  width: 100%;
  margin-bottom: 15px;
  border-radius: 5em;
  cursor: pointer;
  /*transition: .1s;*/
}
._container .container_sticky .content button.schedules {
}
._container .container_sticky .content button.save {
}

</style>

<style>

/* OVERRIDE CSS CHOICES.JS (for select) */
.choices__inner {
  border: none;
  background-color: #f6f7fb;
  border-radius: 10px;
}
.is-open .choices__inner {
  border-radius: 10px 10px 0 0;
}
.choices__list--dropdown {
  border-color: #f6f7fb;
  border-bottom: none;
  background-color: #f6f7fb;
  border-radius: 0 0 10px 10px;
}
.is-open .choices__list--dropdown {
  border-color: #f6f7fb;
}
.choices__list--dropdown .choices__item--selectable.is-highlighted {
  background-color: #e5f4fd;
}
.choices[data-type*="select-one"] .choices__input {
  border-bottom-color: #e5f4fd;
}
.choices__input {
  background-color: transparent;
}

</style>



