
<template>
  <div>
    <EventCheckbox v-for="(value,key) in values" @change="change" :checked="{ key: key, value: value}" :key="key"></EventCheckbox>
  </div>
</template>

<script>

import EventCheckbox from './event_checkbox'
export default {
  name: 'Checkboxs',
  components: {
    EventCheckbox
  },
  data () {
    return {
    }
  },
  props: {
    values: {
      required: true,
      type: Object / Array,
      default: {}
    }
  },
  methods: {
    change (value) {
      switch (value.key) {
        case 'default' :
          if (value.value) {
            this.values.email = false
            this.values.device = false
            this.values.browser = false
          }
          break
        case 'email' :
        case 'device' :
        case 'browser' :
          if (value.value) {
            this.values.default = false
          }
          break
      }
      // if (this.values.email == this.values.default == this.values.device == false)
      this.values[value.key] = value.value
      this.$emit('change', this.$vnode.key, this.values)
    }
  }
}

</script>



