
<template>

  <!-- MY PROFILE -->
  <div id="page_myprofile">

    <div class="myprofile"><div>

      <Header class="header no-select" :current_user="currentUser"></Header>

      <div class="menu no-select">
        <div v-for="(item, key) in menu" class="item" :class="{ 'selected': item.code == codePgSelected }" @click="showPage(item.code)" :key="key">
          <!-- <span v-if="item.icon"><i :class="item.icon"></i></span> -->
          <span class="icon_menu">
            <img :src="item.icon.light">
            <img :src="item.icon.bold">
          </span>
          <span>{{ item.label }}</span>
        </div>
      </div>

      <div class="body">

        <!-- <router-view class="pages"></router-view> -->

        <Notifications v-if="codePgSelected == 'notifications'" class="pages" @submit="submit"></Notifications>

        <Interface v-if="codePgSelected == 'interface'" class="pages" @submit="submit"></Interface>

        <Password v-if="codePgSelected == 'password'" class="pages" @submit-password="submitPassword"></Password>

        <Infos v-if="codePgSelected == 'infos'" class="pages" @submit="submit"></Infos>

      </div>

    </div></div>
  </div>

</template>

<script>

import { api } from '@/api'
import Header from './header/header'
import Infos from './pages/infos/infos'
import Password from './pages/password/password'
import Interface from './pages/interface/interface'
import Notifications from './pages/notifications/notifications'

export default {
  name: 'Index',
  components: {
    Header,
    Infos,
    Password,
    Interface,
    Notifications
  },
  data () {
    return {
      currentUser: window.current_user,
      codePgSelected: this.$route.params.tab,
      menu: window.menu
    }
  },
  watch: {
    '$route' () {
      this.codePgSelected = this.$route.params.tab
    }
  },
  methods: {
    showPage (codePage) {
      this.$router.push(codePage)
    },
    submit (formData, section, updateType, onSuccess = null, onError = null) {
      const data = {update_type: updateType}
      data[section] = this.getParsedData(formData)

      api.updateCollaborator(data).then(res => {
        if (onSuccess !== null) {
          onSuccess(res)
        }
      }, rej => {
        if (onError !== null) {
          onError(rej)
        }
      })
    },
    submitPassword (formData) {
      const data = {user: this.getParsedData(formData)}
      if (data.user.password === '' || data.user.password_confirmation === '') {
        window.notifyWithTimeout('error', window.I18n.t('register.error_passwords_empty'), 1500)
      } else if (data.user.password !== data.user.password_confirmation) {
        window.notifyWithTimeout('error', window.I18n.t('register.error_passwords_mismatch'), 1500)
      } else if (data.user.current_password === '') {
        window.notifyWithTimeout('error', window.I18n.t('register.password_update_tip'), 1500)
      } else {
        api.updatePassword(data).then(res => {
          window.notifyWithTimeout('success', window.I18n.t('collaborators.edit.password_update_success'), 1500)
          setTimeout(function () {
            document.location.reload()
          }, 1500)
        }, rej => {
          window.notifyWithTimeout('error', window.I18n.t('collaborators.edit.password_update_error'), 1500)
        })
      }
    },
    getParsedData (formData) {
      const data = {}
      for (let [key, val] of formData.entries()) {
        if (typeof data[key] === 'undefined') {
          data[key] = val
        } else if (typeof data[key] === 'object') {
          data[key].push(val)
        } else {
          data[key] = [data[key], val]
        }
      }
      return data
    }
  }
}

</script>

<style>

/* VARIABLES */
#page_myprofile {
  --color-guest-message-text: #515365;
}
.no-select {
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none;
}
/**/
#page_myprofile {
  position: absolute;
  display: flex;
  height: 100%;
  width: 100%;
  left: 0px;
  top: 0px;
  font-size: 15px;
  color: #515365;
  color: var(--color-text);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-shadow: 1px 1px 1px rgba(0,0,0,.004);
  font-family: 'Nunito', sans-serif;
  background-color: #f6f7fb;
  overflow: auto;
}
#page_myprofile .myprofile {
  display: flex;
  flex-direction: column;
  width: 900px;
  min-height: 0;
  color: #515365;
  color: var(--color-guest-message-text);
}
#page_myprofile .myprofile > div {
  margin: 40px 10px;
}

/* HEADER */
#page_myprofile .myprofile .header {
  padding: 25px;
}

/* MENU */
#page_myprofile .myprofile .menu {
  display: flex;
  flex-direction: row;
  margin: 25px 0 40px 0;
}
#page_myprofile .myprofile .menu .item {
  display: flex;
  align-items: center;
  height: 40px;
  background-color: #fff;
  border-radius: 10px;
  padding: 0 25px;
  cursor: pointer;
  transition: .1s;
}
#page_myprofile .myprofile .menu .item span {
  opacity: .5;
  transition: inherit;
  white-space: nowrap;
}
#page_myprofile .myprofile .menu .item span:first-child {
  margin-right: 5px;
}
#page_myprofile .myprofile .menu .item.selected {
  background-color: #ebeef6;
}
#page_myprofile .myprofile .menu .item.selected span {
  opacity: 1;
  font-weight: bold;
}
#page_myprofile .myprofile .menu .item:not(:last-child) {
  margin-right: 20px;
}
#page_myprofile .myprofile .menu .item .icon_menu {
  position: relative;
  display: inline-block;
  height: 14px;
  width: auto;
}
#page_myprofile .myprofile .menu .item .icon_menu img {
  position: absolute;
  height: 100%;
  width: auto;
  left: 0px;
  top: 0px;
  object-fit: contain;
  object-position: center;
  transition: inherit;
}
#page_myprofile .myprofile .menu .item .icon_menu img:first-child {
  position: relative;
  vertical-align: top;
}
#page_myprofile .myprofile .menu .item .icon_menu img:last-child,
#page_myprofile .myprofile .menu .item.selected .icon_menu img:first-child {
  opacity: 0;
}
#page_myprofile .myprofile .menu .item.selected .icon_menu,
#page_myprofile .myprofile .menu .item .icon_menu img:first-child,
#page_myprofile .myprofile .menu .item.selected .icon_menu img:last-child {
  opacity: 1;
}

/* BODY */
#page_myprofile .myprofile .body {
  display: flex;
  width: 100%;
  border-radius: 25px;
  flex-direction: column;
  margin-bottom: 60px;
}

/* RESPONSIVE */
@media screen and (max-width: 1000px) {
  #page_myprofile .myprofile {
    width: 800px;
  }
}
@media screen and (max-width: 800px) {
  #page_myprofile .myprofile {
    width: 720px;
  }
}
@media screen and (max-width: 768px) {
  #page_myprofile .myprofile {
    width: 100%;
  }
  #page_myprofile .myprofile .body ._container {
    flex-direction: column;
  }
  #page_myprofile .myprofile .menu {
    flex-wrap: wrap;
  }
  #page_myprofile .myprofile .menu .item {
    padding: 0 15px;
    margin-bottom: 10px;
  }
  #page_myprofile .myprofile .menu .item:not(:last-child):not(:nth-child(2)) {
    margin-right: 10px;
  }
  #page_myprofile .myprofile .body ._container .container_sticky {
    width: 100%;
    margin: 10px 0 0 0;
  }
  ._container .container_sticky .content .illustration {
    display: none;
  }
  ._container .form {
    padding: 20px;
  }
  ._container .form .container_form .line {
    flex-direction: column;
    margin: 0px;
  }
  ._container .form .container_form .line > div,
  ._container .form .container_form .line > div > label > div {
    margin: 0px;
  }
  ._container .form .container_form .line > div {
    margin-bottom: 20px;
  }
  ._container .avatar {
    height: 70px !important;
    width: 70px !important;
  }
  ._container .infos {
    margin-left: 15px;
  }
  ._container .infos .name {
    font-size: 1.3em !important;
  }
  #page_myprofile .myprofile > div {
    margin: 20px 20px 40px 20px;
  }
  #page_myprofile .myprofile .header {
    padding: 20px;
  }
  #page_myprofile ._container .form {
    padding: 20px;
  }
  #page_myprofile .myprofile .menu {
    margin: 10px 0 30px 0;
  }
  ._container .container_sticky .content .container_captions {
    display: none;
  }
  .groups .group_notifs .title {
    padding: 0 0 0 12px;
  }
  .groups .group_notifs .title .icon {
    width: 15px;
  }
  .group_notifs .container_notifs .notifications .item_notif {
    padding: 0 0 0 40px !important;
  }
  .group_notifs .container_notifs .notifications .item_notif {
    height: auto !important;
  }
  .group_notifs .container_notifs .notifications .item_notif ._label {
    white-space: normal !important;
  }
  ._container .groups .restore {
    padding-left: 12px !important;
  }
}

</style>



