
<template>

  <div class="_caption" :class="{ 'open': open }" v-click-outside="close" @click="toggle()">
    <div class="title">
      <span class="icon"><i :class="caption.icon"></i></span>
      <span class="name">{{ caption.name }}</span>
    </div>
    <div class="description">{{ caption.description }}</div>
    <div class="show_more">
      <span>{{ open ? text.hide : text.show_more }}</span>
    </div>
  </div>

</template>

<script>

import ClickOutside from 'vue-click-outside'
export default {
  name: 'Caption',
  directives: {
    ClickOutside
  },
  props: {
    caption: {
      required: true,
      type: Object / Array,
      default: {
        name: 'Name',
        icon: 'Icon',
        description: 'Description'
      }
    }
  },
  data () {
    return {
      text: {
        show_more: window.I18n.t('handler.slack.show_more'),
        hide: window.I18n.t('conversations.index.hide')
      },
      open: false
    }
  },
  methods: {
    toggle () {
      this.open = !this.open
    },
    close () {
      this.open = false
    }
  }
}

</script>

<style scoped>

._caption {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  padding: 20px 25px;
}
.container_caption:first-child ._caption {
  border-radius: 10px 10px 0 0;
}
.container_caption:last-child ._caption {
  border-radius: 0 0 10px 10px;
}
.container_caption:not(:last-child) ._caption {
  margin-bottom: 1px;
}
.container_caption ._caption.open {
  width: 250%;
  left: -150%;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  box-shadow: 0 0 5px #0001;
  z-index: 10;
}
._caption .title {
  margin-bottom: 6px;
}
._caption .title .icon {
  font-size: 1.4em;
  margin-right: 5px;
}
._caption .title .label {
  font-weight: bold;
}
._caption .description {
  font-size: .8em;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  transition: .1s;
}
._caption.open .description {
  -webkit-line-clamp: initial;
}
._caption .show_more {
  margin-top: 4px;
}
._caption .show_more span {
  font-size: .8em;
  opacity: .7;
  margin-top: 10px;
  cursor: pointer;
  font-weight: normal;
}
._caption .show_more span:hover {
  text-decoration: underline;
}

</style>



