
<template>
  <div class="_container gradient_header">

    <label for="avatar" class="avatar">
      <img :src="current_user.avatar" class="preview_avatar">
      <div class="avatar_hover">
        <svg xmlns="http://www.w3.org/2000/svg" class="color_fill" viewBox="0 0 512 512"><g class="fa-group"><path class="fa-secondary" fill="currentColor" d="M344 288a88 88 0 1 1-88-88 88.13 88.13 0 0 1 88 88z" opacity="0.4"></path><path class="fa-primary" fill="currentColor" d="M464 96h-88l-12.4-32.9A47.93 47.93 0 0 0 318.7 32H193.2a47.93 47.93 0 0 0-44.9 31.1L136 96H48a48 48 0 0 0-48 48v288a48 48 0 0 0 48 48h416a48 48 0 0 0 48-48V144a48 48 0 0 0-48-48zM256 408a120 120 0 1 1 120-120 120.1 120.1 0 0 1-120 120z"></path></g></svg>
      </div>
    </label>
    <div class="infos">
      <div class="name">{{ current_user.name }}</div>
      <div class="text">{{ current_user.email }}</div>
      <div class="text">{{ current_user.role }}</div>
    </div>

  </div>
</template>

<script>

export default {
  name: 'Header',
  props: {
    current_user: {
      required: true,
      type: Object / Array,
      default: {}
    }
  }
}

</script>

<style scoped>

._container {
  display: flex;
  width: 100%;
  height: auto;
  flex-direction: row;
  background-color: #27C469;
  background-image: linear-gradient(200deg, #52de8f, #27C469);
  border-radius: 20px;
}
._container .avatar {
  position: relative;
  width: 90px;
  height: 90px;
  margin-bottom: 15px;
  background-color: rgba(255,255,255,.7);
  border-radius: 50%;
  overflow: hidden;
  cursor: pointer;
}
._container .avatar img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: inherit;
}
._container .avatar .avatar_hover {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0px;
  top: 0px;
  transition: .1s;
  background-color: rgba(255,255,255,.8);
  opacity: 0;
}
._container .avatar .avatar_hover svg {
  position: relative;
  height: 20px;
  width: 20px;
  left: 50%;
  top: 50%;
  transition: inherit;
  transform: translate(-50%, -50%) scale(1);
}
._container .avatar:hover .avatar_hover {
  opacity: 1;
}
._container .avatar:hover .avatar_hover svg {
  transform: translate(-50%, -50%) scale(1.5);
}
._container .avatar .input_file {
  opacity: 0;
  position: absolute;
}
._container .infos {
  margin-left: 20px;
  color: #fff;
}
._container .infos .name {
  font-size: 1.5em;
}
._container .infos .text {
  opacity: .95;
}

</style>



