
<template>
  <form class="_container" @submit.prevent.stop="submit">

    <!-- GROUPS -->
    <div class="groups">
      <NotificationsGroup v-for="(group, key) in notifications.groups" :group="group" :captions="notifications.captions" @change="change" @selectAllColumn="selectAllColumn" :closable="key !== 0" :key="key"></NotificationsGroup>

      <!-- <div><a href="#" @click="restoreDefaultSettings" class="restore">{{ text.restore }}</a></div> -->
      <div class="container_btn_restore">
        <button type="button" @click="restoreDefaultSettings" class="btn btn-danger btn-inverse restore">{{ text.restore }}</button>
      </div>
    </div>

    <!-- STICKY -->
    <div class="container_sticky no-select">
      <div class="content">
        <button type="submit" class="btn btn-success button_save">{{ text.save }}</button>
        <button type="reset" class="btn btn-danger btn-inverse btn_cancel">{{ text.cancel }}</button>
        <captions :captions="notifications.captions" class="container_captions"></captions>
      </div>
    </div>

  </form>
</template>

<script>

import { api } from '@/api'
import Captions from './captions/captions'
import NotificationsGroup from './notifications_group/notifications_group'
export default {
  name: 'Notifications',
  components: {
    Captions,
    NotificationsGroup
  },
  data () {
    return {
      text: {
        restore: window.I18n.t('collaborators.edit.notifications_page.restore'),
        cancel: window.I18n.t('onboarding.buttons.cancel'),
        save: window.I18n.t('onboarding.buttons.save')
      },
      notifications: window.notifiable_events_settings
    }
  },
  methods: {
    change (key, value) {
      var keys = key.split('_')
      switch (keys[0]) {
        case 'paramNotif' :
          this.notifications.groups[keys[1]].list[keys[2]].value = value
          break
        case 'option' :
          this.notifications.groups[keys[1]].list[keys[2]].options[keys[3]].value = value
          break
      }
    },
    selectAllColumn (key) {
      var checkeds = false
      for (var i = 0; i < this.notifications.groups[key[0]].list.length; i++) {
        if (!this.notifications.groups[key[0]].list[i].value[key[1]] && !checkeds) {
          checkeds = true
        }
      }
      for (var j = 0; j < this.notifications.groups[key[0]].list.length; j++) {
        switch (key[1]) {
          case 'default' :
            if (checkeds) {
              this.notifications.groups[key[0]].list[j].value.email = false
              this.notifications.groups[key[0]].list[j].value.device = false
              this.notifications.groups[key[0]].list[j].value.browser = false
            }
            break
          case 'email' :
          case 'device' :
          case 'browser' :
            if (checkeds) {
              this.notifications.groups[key[0]].list[j].value.default = false
            }
            break
        }
        this.notifications.groups[key[0]].list[j].value[key[1]] = checkeds
      }
    },
    submit () {
      var settings = {}
      for (let group of this.notifications['groups']) {
        for (let item of group['list']) {
          settings[item['id']] = item['value']
          if (typeof item['options'] !== 'undefined') {
            for (let option of item['options']) {
              settings[option['id']] = option['value']
            }
          }
        }
      }
      api.updateNotifiableEventsSettings({settings: settings}).then(res => {
        window.notifyWithTimeout(res.data.success ? 'success' : 'error', window.I18n.t('collaborators.edit.notif_items_update_' + (res.data.success ? 'success' : 'error'), 1500))
      })
    },
    restoreDefaultSettings() {
      api.getDefaultNotifiableEventsSettings().then(res => {
        // debugger
        this.notifications.groups = res.data.data.groups
      })
    }
  }
}

</script>

<style scoped>

._container {
  display: flex;
  flex-direction: row;
}
._container .groups {
  display: flex;
  flex-direction: column;
  flex: 1;
}
._container .groups .container_btn_restore {
  text-align: right;
}
._container .groups .container_btn_restore .restore {
  display: inline-block;
  opacity: .9;
  font-size: .9333em;
  color: inherit;
  background-color: #ebeef6;
  color: #515365;
}
._container .groups .container_btn_restore .restore:hover {
  opacity: 1;
  background-color: #dee2ec;
  color: #515365;
}
._container .container_sticky {
  margin-left: 25px;
  width: 200px;
}
._container .container_sticky .content {
  position: sticky;
  top: 15px;
}
._container .container_sticky .content button {
  display: block;
  width: 100%;
  margin-bottom: 15px;
  border-radius: 5em;
  cursor: pointer;
  transition: .1s;
}
._container .container_sticky .content button.btn_cancel {
  background-color: #ebeef6;
  color: #515365;
}
._container .container_sticky .content button.btn_cancel:hover {
  background-color: #dee2ec;
  color: #515365;
}

</style>



