import { HTTP } from '@/http-common'

export const api = {
  updateNotifiableEventsSettings: (data) => { return HTTP.post(`/collaborators/${window.current_user.uid}/update_notifications_settings`, data) },
  updateCollaborator: (data) => { return HTTP.put(`/collaborators/${window.current_user.uid}`, data) },
  updatePassword: (data) => { return HTTP.put(`${window.password_update_url}`, data) },
  getDefaultNotifiableEventsSettings: () => { return HTTP.get(`/collaborators/${window.current_user.uid}/get_default_notifiable_events_settings`) }
}



