
<template>
  <form class="_container" action="" method="post" accept-charset="UTF-8" ref="form_password" @submit.prevent.stop="submit">

    <div class="form">
      <Form :form="form"></Form>
    </div>

    <div class="container_sticky">
      <div class="content">
        <button type="submit" class="btn btn-success save" border-efx="true">{{ buttons.save }}</button>
        <img :src="image" class="illustration">
      </div>
    </div>

  </form>
</template>

<script>

import Form from '@/components/form/form'
export default {
  name: 'Password',
  components: {
    Form
  },
  data () {
    return {
      buttons: {
        save: window.I18n.t('register.password_update')
      },
      form: [
        [
          { type: 'password', label: window.I18n.t('collaborators.edit.current_password'), name: 'current_password', value: '' }
        ],
        [
          { type: 'password', label: window.I18n.t('collaborators.edit.new_password'), name: 'password', value: '' }
        ],
        [
          { type: 'password', label: window.I18n.t('collaborators.edit.new_password_confirmation'), name: 'password_confirmation', value: '' }
        ],
        [
          { type: 'hidden', name: 'utf8', value: 'true' },
          { type: 'hidden', name: '_method', value: 'put' },
          { type: 'hidden', name: 'authenticity_token', value: '' }
        ]
      ],
      image: window.appName === 'Slaask' ? '/edit/edit-password-slaask.svg' : '/edit/edit-password-xeno.svg'
    }
  },
  methods: {
    submit () {
      this.$emit('submit-password', new FormData(this.$refs['form_password']))
    }
  }
}

</script>

<style scoped>

._container {
  display: flex;
  flex-direction: row;
}
._container .form {
  background-color: #fff;
  border-radius: 10px;
  padding: 30px;
  flex: 1;
}
._container .container_sticky {
  margin-left: 25px;
  width: 250px;
}
._container .container_sticky .content {
  position: sticky;
  top: 15px;
}
._container .container_sticky .content .illustration {
  width: 100%;
}
._container .container_sticky .content button {
  display: block;
  width: 100%;
  margin-bottom: 15px;
  border: none;
  border-radius: 5em;
  background-color: #27C469;
  color: #fff;
  cursor: pointer;
  transition: .1s;
}
._container .container_sticky .content button.schedules {
}
._container .container_sticky .content button.save {
}

</style>



