
<template>
  <div>

    <div v-for="(caption, key) in captions" class="container_caption" :key="key">

      <Caption :caption="caption"></Caption>

    </div>

  </div>
</template>

<script>

import Caption from './caption'
export default {
  name: 'Captions',
  components: {
    Caption
  },
  props: {
    captions: {
      required: true,
      type: Object / Array,
      default: {
        name: 'Name',
        icon: 'Icon',
        description: 'Description'
      }
    }
  },
  data () {
    return {
      text: {
        show_more: window.I18n.t('handler.slack.show_more')
      }
    }
  }
}

</script>



