
<template>
  <form class="_container" action="" method="post" ref="form_interface" @submit.prevent.stop="submit">

    <div class="form">
      <Form :form="form"></Form>
    </div>

    <div class="container_sticky">
      <div class="content">
        <button type="submit" class="btn btn-success save" border-efx="true">{{ buttons.save }}</button>
        <img :src="image" class="illustration">
      </div>
    </div>

  </form>
</template>

<script>

import Form from '@/components/form/form'
export default {
  name: 'Interface',
  components: {
    Form
  },
  data () {
    return {
      text: {
        restore: window.I18n.t('collaborators.edit.notifications_page.restore')
      },
      buttons: {
        save: window.I18n.t('onboarding.buttons.save')
      },
      form: [
        [
          { type: 'select', label: window.I18n.t('team.edit.change_profile_language'), name: 'interface_language', value: window.current_user.interface_language, optgroup: [{ label: window.I18n.t('collaborators.edit.official_languages'), options: window.found_official }, { label: window.I18n.t('collaborators.edit.beta_languages'), options: window.languages }], ref: 'selectInterfaceLanguage' }
        ],
        [
          {
            type: 'list-checkbox',
            label: window.I18n.t('collaborators.edit.menus_intro'),
            list: window.available_menus
          }
        ],
        [
          {
            type: 'list-checkbox',
            label: window.I18n.t('collaborators.edit.keyboard_shortcuts'),
            list: [
              { type: 'checkbox', text: window.I18n.t('collaborators.edit.inbox_keyboard_shortcuts'), name: 'shortcut', checked: true }
            ]
          }
        ]
      ],
      image: window.appName === 'Slaask' ? '/edit/edit-menu-slaask.svg' : '/edit/edit-menu-xeno.svg'
    }
  },
  methods: {
    submit () {
      this.$emit('submit', new FormData(this.$refs['form_interface']), 'user', 'interface', res => {
        window.notifyWithTimeout(res.data.success ? 'success' : 'error', window.I18n.t(`collaborators.edit.menu_items_update_${res.data.success ? 'success' : res.data.error}`), 1500)
        if (res.data.success) {
          setTimeout(() => { document.location.reload() }, 1500)
        }
      })
    }
  }
}

</script>

<style scoped>

._container {
  display: flex;
  flex-direction: row;
}
._container .form {
  background-color: #fff;
  border-radius: 10px;
  padding: 30px;
  flex: 1;
}
._container .container_sticky {
  margin-left: 25px;
  width: 250px;
}
._container .container_sticky .content {
  position: sticky;
  top: 15px;
}
._container .container_sticky .content .illustration {
  width: 100%;
}
._container .container_sticky .content button {
  display: block;
  width: 100%;
  padding: 8px 10px;
  margin-bottom: 15px;
  border: none;
  border-radius: 5em;
  background-color: #27C469;
  color: #fff;
  cursor: pointer;
  transition: .1s;
}
._container .container_sticky .content button.schedules {
}
._container .container_sticky .content button.save {
}

</style>



