import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/infos'
    },
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/:tab',
      name: 'IndexTab',
      component: Index
    }
  ]
})



