
<template>

  <label class="_container">
    <input type="checkbox" :name="name" :checked="activate" :disabled="disabled" @change="change" style="display: none;">
    <div class="checkbox">
      <span class="box"></span>
    </div>
  </label>

</template>

<script>

export default {
  name: 'Checkbox',
  data () {
    return {
      activate: false
    }
  },
  props: {
    checked: {
      required: false,
      type: Object / Array,
      default: {}
    },
    disabled: {
      required: false,
      type: Boolean,
      default: false
    },
    name: {
      required: false,
      type: String,
      default: ''
    }
  },
  mounted () {
    this.activate = this.checked
  },
  methods: {
    change () {
      if (this.disabled) return
      this.activate = !this.activate
      this.$emit('input', this.activate)
      // this.$emit('change', this.activate)
      this.$emit('change', this.$vnode.key, this.activate)
    }
  }
}

</script>

<style scoped>

._container {
  cursor: pointer;
  display: flex;
  width: 40px;
  height: 100%;
  align-items: center;
  justify-content: center;
  border-left: 1px solid #f6f7fb;
  flex: 0 !important;
}
.checkbox {
  --input-color: #6bbaf0;
  --input-color--line: #fff;
  --input-color--border: rgba(0, 0, 0, .08627);
}
.checkbox {
  display: inline;
  font-size: 13px;
  text-rendering: optimizeLegibility;
  box-sizing: content-box;
  cursor: pointer;
}
.checkbox .box {
  display: inline-block;
  width: 4px;
  height: 4px;
  width: 13px;
  height: 13px;
  box-sizing: content-box;
  background-color: var(--input-color--border);
  border: 1px solid transparent;
  transition: background-color .1s ease;
  transition-delay: .05s
}
.checkbox .box {
  border-radius: calc(13px * .266);
  opacity: .9;
}
.checkbox .box:after,
.checkbox .box:before {
  position: absolute;
  content: '';
  width: 0;
  transform-origin: 0 100%;
  border-bottom: 1px solid var(--input-color--line);
}
.checkbox .box:before {
  transition: width .05s linear;
  transition-delay: .1s;
  transform: rotate(45deg) translate(5px, 3px);
}
.checkbox .box:after {
  transition: width .1s linear;
  transition-delay: 0s;
  transform: rotate(-45deg) translate(-2px, 10px);
}

/* CHECKED */
@keyframes wobble {
  0% { transform: scale(1) rotate(0deg) }
  40% { transform: scale(1.1, .9) rotate(5deg) }
  70% { transform: scale(.9, 1.1) rotate(-3deg) }
  80% { transform: scale(1.05, .95) rotate(1deg) }
  to { transform: scale(1) rotate(0deg) }
}
/*.checkbox.checked:not(._disabled) .box {*/
input[type=checkbox]:checked:not(:disabled) ~ .checkbox .box {
  height: 13px;
  width: 13px;
  transition-delay: 0s;
  animation-name: wobble;
  animation-duration: .3s;
  animation-timing-function: ease;
  background-color: var(--input-color);
  border: 1px solid var(--input-color--border);
}
/*.checkbox.checked:not(._disabled) .box:before {*/
input[type=checkbox]:checked:not(:disabled) ~ .checkbox .box:before {
  width: calc(13px / 2 - 2px);
  transition-delay: 0s;
}
/*.checkbox.checked:not(._disabled) .box:after {*/
input[type=checkbox]:checked:not(:disabled) ~ .checkbox .box:after {
  width: calc(13px / 2 + 2px);
  transition-delay: .05s;
}
/* DISABLED */
input[type=checkbox]:disabled ~ .checkbox {
  opacity: .6;
  cursor: not-allowed;
}

</style>



